<template>
  <div class="home">
    <div class="top">
      <select class="selectCity">
        <select-option v-for="city of cityList" :city="city" :key="city.code"></select-option>
      </select>
      <div @click="getProducts" class="getVals">Get product suggestions</div>
    </div> 
    <div class="bottom" v-if="cityProducts">
      <hr>
      <div>City: {{cityProducts.name}}</div>
      <city-days-products v-for="(dateData,day) in cityProducts.dates" 
        :key="day" 
        :date="day"
        :dateData="dateData"
      ></city-days-products>
    </div>
  </div>
</template>

<script>
import selectOption from '@/components/SelectOption.vue';
import CityDaysProducts from '@/components/CityDaysProducts.vue';
import api from '@/api/api.js';
import SelectOptionVue from '../components/SelectOption.vue';
export default {
  name: 'Home',
  props:[],
  components: {
    selectOption,
    CityDaysProducts
  },
  data(){
      return{
          cityList: [],
          cityProducts:null,
      }
  },
  created(){
    this.getCities();
  },
  methods:{
    async getProducts(){
      const selectedVal = document.querySelector('.selectCity').value;
      await api.getRecommendations(
        selectedVal,
        (productObj)=>{
          console.log(productObj)
          this.cityProducts = productObj;
        },
        (error)=>{
          console.log(error)
        }
      )
    },
    async getCities(){
      await api.getCities((cityArr)=>{
        this.cityList = cityArr;
      },
      (error)=>{
        console.log(error)
      });
    }
  }
}
</script>

<style scoped>
  .getVals{
    display: inline-block;
    padding: 5px 1rem;
    margin-left:2rem;
    background:rgb(42, 165, 122);
    color: white;
    cursor: pointer;
    ;
  }
  .top{
    margin-top: 2rem;
  }
</style>
