import axios from 'axios'


const API_URL = 'http://product-api.serverpi.ddns.me/api/';

export default {

  getCities: async(success,failure)=>{
    try {
      const response = await api().get(API_URL + 'cities');
      success(response.data.message);
    } catch (error) {
      failure(error);
    }
  },
  getRecommendations: async(cityCode,success,failure)=>{
    try {
      const response = await api().get(API_URL + 'products/recommended/'+cityCode);
      success(response.data.message);
    } catch (error) {
      failure(error);
    }
  },

}
//--------------------------------------------------------------------------------------------AXIOS HEADER CONFIGURATION
function api() {
    return axios.create({
      headers: {
        'Content-Type': 'application/json'
      }
    });
}

